@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
@import url(//netdna.bootstrapcdn.com/font-awesome/4.0.3/css/font-awesome.min.css);
html,
body {
  width: 98%;
  height: 98%;
  margin: 5px;
}
.DivPag {
  padding: 2px;
  display: grid;
  font-family: sans-serif;
  grid-template-areas:
    "cab"
    "con";
  grid-template-rows: 55px 87vh;
  grid-template-columns: 98vw;
  grid-gap: 2px;
}
.DivCab {
  grid-area: cab;
  /*background-image: linear-gradient(180deg, #00fcfd, #00bdbd);*/
  background-color: rgb(87, 215, 253);
  color: white;
  border-radius: 5px;
  border-color: #000000;
  border-width: 1px 1px 1px 1px;
  border-style: solid;
}
.DivDir {
  grid-area: con;
  background-color: rgb(0, 255, 200);
  color: white;
  border-radius: 5px;
  border-color: #000000;
  border-width: 1px 1px 1px 1px;
  border-style: solid;
}

.cab > span {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 45px;
}
.DivCab .btn {
  left: 10px;
  background-color: red;
  margin-left: 10px;
}

