html,
body {
  width: 98%;
  height: 98%;
  margin: 5px;
}
.DivPag {
  padding: 2px;
  display: grid;
  font-family: sans-serif;
  grid-template-areas:
    "cab"
    "con";
  grid-template-rows: 55px 87vh;
  grid-template-columns: 98vw;
  grid-gap: 2px;
}
.DivCab {
  grid-area: cab;
  /*background-image: linear-gradient(180deg, #00fcfd, #00bdbd);*/
  background-color: rgb(87, 215, 253);
  color: white;
  border-radius: 5px;
  border-color: #000000;
  border-width: 1px 1px 1px 1px;
  border-style: solid;
}
.DivDir {
  grid-area: con;
  background-color: rgb(0, 255, 200);
  color: white;
  border-radius: 5px;
  border-color: #000000;
  border-width: 1px 1px 1px 1px;
  border-style: solid;
}
